.categoryWrap {
    width: 100%;
    min-height: 100vh;
    background-color: var(--base-white);
    position: relative;
}

.visible {
    opacity: 1;
    transition: all ease .2s;
}

.invisible {
    opacity: 0;
    transition: all ease .2s;
}

.categoryTitle {
    margin: 0 auto;
    width: fit-content;
    padding-top: 30px;
    font-family: var(--serif);
    font-size: 1.5rem;
    font-weight: 500;
    color: var(--deep-blue);
}

.topMenuWrap {
    font-family: var(--sans);
    font-size: 1.1rem;
    margin: 0 auto;
    width: fit-content;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    gap: 30px;
}

.topMenuLink {
    color: var(--blue);
    position: relative;
    text-decoration: none;
}

.topMenuLink:after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: -7px;
    width: 0;
    height: 2px; /* Высота линии */
    background-color: var(--blue); /* Цвет подчеркивания при исчезании линии*/
    transition: width 0.5s; /* Время эффекта */
}

.topMenuLink:hover:after {
    content: "";
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    bottom: -7px;
    height: 2px; /* Высота линии */
    background-color: var(--blue); /* Цвет подчеркивания при появлении линии*/
    transition: width 0.5s; /* Время эффекта */
}
