@import url('https://fonts.googleapis.com/css2?family=STIX+Two+Text:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

:root {
    --sans: 'Montserrat', sans-serif;
    --serif: 'STIX Two Text', serif;
    --base-black: #111;
    --base-white: #fffff5;
    --light-cyan: #80f8ff;
    --blue: #2e65c3;
    --deep-blue: #122342;
    --green: #008c75;
    --ex-light-grey: hsl(50, 10%, 90%);
    --light-grey: hsl(50, 10%, 75%);
    --semi-light-grey: #aaa;
    --semi-grey: #888;
    --dark-grey: #646464;
    --ex-light-beige: #f5f5e9;
    --light-beige: #e0e0d1;
    --semi-light-beige: #ebebdb;
    --semi-beige: #d4d4c6;
    --brown-grey: #877059;
    --light-orange: #ffd580;
    --peach: #dc8b51;
    --semi-orange: #ffb042;
    --orange: #ee7700;
    --dark-orange: #d92400;
    --upcoming-bg: #c5ede4;
    --now-bg: #fafab6;
    --completed-bg: #d5f2cb;
    --user-cacelled-bg: #e3e5e5;
    --admin-cancelled-bg: #e5dcda;
}

.blue {
    color: var(--blue);
}

.green {
    color: var(--green);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
}

body {
    font-family: var(--sans);
    -webkit-font-smoothing: antialiased;
}

.container {
    min-height: 100%;
    position: relative;
}

textarea,
button,
input,
select {
    font-family: inherit;
    color: inherit;
    font-size: inherit;
    border: none;
    outline: none;
}


a {
    color: inherit;
    text-decoration: none;
}

.no_wrap {
    white-space: nowrap;
}

.fw300 {
    font-weight: 300;
}

.fw400 {
    font-weight: 400;
}

.fw500 {
    font-weight: 500;
}

.fw600 {
    font-weight: 600;
}

.fw700 {
    font-weight: 700;
}
